import {container, cardTitle, title} from "../../assets/jss/material-kit-react"
import imagesStyle from "../../assets/jss/material-kit-react/imagesStyles.js";

const styles = (theme) => ({
    container: {
        ...container,
        marginTop: theme.mixins.toolbar.minHeight * 2
    },
    title: {
        ...title,
        lineHeight: "30px",
        fontSize: "18px",
        borderRadius: "3px",
        textTransform: "none",
        color: "inherit",
        padding: "8px 16px",
        letterSpacing: "unset",
        "&:hover,&:focus": {
            color: "inherit",
            background: "transparent"
        }
    },
    section: {
        padding: "70px 0",
        textAlign: "center"
    },
    ...imagesStyle,
    itemGrid: {
        marginLeft: "auto",
        marginRight: "auto"
    },
    cardTitle: {
      ...cardTitle,
      textAlign: "center"
    },
    smallTitle: {
        color: "#6c757d"
    },
    description: {
        color: "#999",
        textAlign: "justify"
    },
    justifyCenter: {
        justifyContent: "center !important"
    },
    socials: {
        marginTop: "0",
        width: "100%",
        transform: "none",
        left: "0",
        top: "0",
        height: "100%",
        lineHeight: "41px",
        fontSize: "20px",
        color: "#999"
    },
    margin5: {
        margin: "5px"
    }
});

export default styles;
